@import "../../assets/styles/app";

.enterCode {
  height: 100%;
  display: flex;
  flex-direction: column;
  //justify-content: center;
  position: relative;
  padding-top: 24px;

  @include desktop2 {
    align-items: center;
    padding-top: $k * 24px;
  }

  &_paddingBottom {
    padding-bottom: 60px;

    @include desktop2 {
      padding-bottom: 0;
    }
  }

  .title {
    align-self: center;
    width: 201px;
    height: 32px;
    margin-top: 16px;

    @include desktop2 {
      width: $k * 440px;
      height: $k * 72px;
      margin-top: 0;
    }

    svg {
      width: 100%;
      height: 100%;
    }
  }

  .subTitle {
    margin-top: 12px;
    font-weight: 700;
    font-size: 12px;
    line-height: 140%;
    text-transform: uppercase;
    color: $color_main_100;
    text-align: center;

    @include desktop2 {
      font-size: $k * 18px;
      margin-top: $k * 12px;
    }
  }

  .texts {
    margin-top: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include desktop2 {
      margin-top: $k * 24px;
    }

    p:nth-child(1) {
      font-weight: 700;
      font-size: 12px;
      line-height: 140%;
      text-transform: uppercase;
      color: $color_body_50;

      @include desktop2 {
        font-size: $k * 12px;
      }
    }

    svg {
      width: 161px;
      height: 16px;
      margin-top: 8px;

      @include desktop2 {
        margin-top: $k * 4px;
        width: $k * 169px;
        height: $k * 24px;
      }
    }

    p:nth-child(3) {
      margin-top: 24px;
      font-weight: 700;
      font-size: 12px;
      line-height: 140%;
      text-transform: uppercase;
      color: $color_main_100;

      @include desktop2 {
        margin-top: $k * 24px;
        font-size: $k * 12px;
      }
    }

    p:nth-child(4) {
      margin-top: 8px;
      font-weight: 700;
      font-size: 18px;
      line-height: 140%;
      text-transform: uppercase;
      color: $color_main;

      @include desktop2 {
        margin-top: $k * 8px;
        font-size: $k * 18px;
      }
    }
  }

  .topUsers {
    margin-top: 24px;

    @include desktop2 {
      margin-top: $k * 24px;
    }

    & > div:nth-child(2) {
      @include desktop2 {
        height: calc(100vh - 57.6px - 28.8px - 86.4px - 14.4px - 30.2px - 28.8px - 176.2px - 28.8px - 142.6px - 28.8px - 43.2px - 2px);
        overflow-y: auto;
      }
    }
  }

}